<template>
<div class="fp-section-content">
    <slot></slot>
</div>
</template>

<script type="text/javascript">
import fullpage from "notone"

require('notone/fullpage.css')

export default {
    mounted () {
        fullpage.init('.fp-section-content',{
            threshold: 400,       // 
            pageSpeed: 600,      //  ms
            autoScroll: 0,      //  0  ms
            loopSection: false,          // Section
            loopSlide: true,     // Slide
            afterLoad: null,     //  afterLoad 
            beforeLeave: null,    //  beforeLeave 
            afterSlideLoad: null,       // slide 
            beforeSlideLeave: null      // slide 
        })
    }
}
</script>
<style type="text/css" lang="less">
.fp-section-content {
    .fp-section {
        min-height: 100vh;
    }
}
</style>


